@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
}

/* バックグラウンドカラー */
body {
  background-color: #111;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.bg-line--top {
  position: relative;
}

.bg-line--top::before {
  content: "";
  display: inline-block;
  width: 98vw;
  height: 1px;
  background-color: #F9F871;
  z-index: -1;
  position: absolute;
  top: 1.25rem;
  left: 0;
  transform: skewY(61.09deg);
  transform-origin: left 0;
}
@media screen and (min-width: 768px) {
  .bg-line--top::before {
    top: -6.25rem;
    left: 0;
    transform: skewY(45deg);
    transform-origin: left 0;
  }
}

.bg-line--bottom {
  position: relative;
}

.bg-line--bottom::before {
  content: "";
  display: inline-block;
  width: 100vw;
  height: 1px;
  background-color: #F9F871;
  z-index: -1;
  position: absolute;
  top: 0rem;
  right: 8.75rem;
  transform: skewY(-74.95deg);
  transform-origin: right 0;
}
@media screen and (min-width: 768px) {
  .bg-line--bottom::before {
    transform: skewY(-45deg);
    top: -12.1875rem;
    right: 0;
  }
}

.btn {
  border: 1px solid #fff;
  font-size: 1.25rem;
  letter-spacing: 0.1em;
  padding: 0.625rem;
  width: 100%;
  display: inline-block;
  text-align: center;
  background-color: #333;
  color: #fff;
  font-weight: 300;
}
@media screen and (min-width: 768px) {
  .btn {
    font-size: 1rem;
    padding: 0.9375rem 6.125rem;
    position: relative;
    overflow: hidden;
    z-index: 1;
    max-width: 22.5rem;
  }
  .btn:hover {
    color: #333;
    z-index: 1;
  }
  .btn::before {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: "";
    -webkit-transition: 0.3s all linear;
    transition: 0.3s all linear;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    background: #fff;
    z-index: -1;
  }
  .btn:hover::before {
    -webkit-transform: translateX(0%);
    transform: translateX(0%);
  }
}

