@use "global" as *;

.news {
  color: $white;
  margin-bottom: rem(101);
  @include mq(md) {
    margin-bottom: rem(180);
  }
}

.news__inner {
  width: 100%;
  padding-right: $padding-sp;
  padding-left: $padding-sp;
  margin-right: auto;
  margin-left: auto;
  max-width: rem(640);
  @include mq(md) {
    max-width: rem(1092);
    padding-right: $padding-pc;
    padding-left: $padding-pc;
  }
}

.news__content {
  @include mq(md) {
    display: flex;
    align-items: start;
  }
}

.news__contents {
  @include mq(md) {
    display: flex;
  }
}

.news__info {
  display: flex;
  align-items: center;
  @include mq(md) {
    margin-right: rem(55);
  }
}

.news__date {
  display: block;
  margin-right: rem(32);
  font-size: rem(16);
  @include mq(md) {
    margin-right: rem(48);
  }
}

.news__category {
  font-size: rem(14);
  border: 1px solid $white;
  padding: rem(5) rem(20);
}

.news__block {
  margin-top: rem(14);
  max-width: rem(572);
  @include mq(md) {
    margin-top: rem(4);
    // margin-right: rem(35);
  }
}


.news__text a {
  font-size: rem(16);
  @include mq(md) {
    &:hover {
      border-bottom: 1px solid $white;
    }
  }
}

.news__more {
  margin-top: rem(26);
  @include mq(md) {
    margin: 0 0 0 auto;
  }
}

.news__more a {
  border: 1px solid $white;
  font-size: rem(20);
  letter-spacing: 0.1em;
  padding: rem(10) ;
  width: 100%;
  display: block;
  text-align: center;
  background-color: #333;
  @include mq(md) {
    border: none;
    background-color: $white;
    color: $black;
    font-weight: $normal;
    font-size: rem(16);
    letter-spacing: 0;
    padding: rem(4) rem(27);
    position: relative;
    overflow: hidden;
    z-index: 1;
    &:hover {
      color: #fff;
      z-index: 1;
    }
    &::before {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      content: '';
      transition: .3s all  linear;
      transform: translateX(-100%);
      background: #333333;
      z-index: -1;
    }
    &:hover::before {
      transform: translateX(0%);
    }
  }
}



